
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/share';
import 'rxjs/add/observable/of'; //proper way to import the 'of' operator



interface ICoin {
    name: string;
    symbol: number;
    price_usd: string;
    market_cap_usd : string;
    percent_change_24h : string;
    available_supply: string;
    
  }
  @Injectable()  
export class CoinListService {

  constructor(private http: HttpClient){}
  private data: any;
  private observable: Observable<any>;;
  
  
     getListOfCrypto() {
      this.http.get<ICoin[]>('https://api.coinmarketcap.com/v1/ticker/').subscribe(
            data => {
              //console.log(data);      
              //console.log(this.coins);      
              //console.log(this.coins[0].name);
            }   
            
     )}
    getCoins() : Observable<any> {
        
            const url = `http://URL_TO_API/objects`;
        
            if(!this.data || !this.data.length) {
              // We don't have any saved objects yet, make API call
              console.log("Getting the objects through API call");
              this.observable = this.http.get<ICoin[]>('https://api.coinmarketcap.com/v1/ticker/')
              .map(response =>  {
                // when the cached data is available we don't need the `Observable` reference anymore
                this.observable = null;
                  this.data = response;
                  return this.data;
                }
                // make it shared so more than one subscriber can get the result
              )
              .share();
              return this.observable;
            }else if(this.observable) {
                // if `this.observable` is set then the request is in progress
                // return the `Observable` for the ongoing request
                console.log("Getting observable");
                
                return this.observable;}
            else {
              // Don't need to call the API
              console.log("No API");
              return Observable.of(this.data);
            
            
            }
          }
        


}


