import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';


import { AppComponent } from './app.component';
import { CryptolistComponent } from './cryptolist/cryptolist.component';
import { CryptodetailComponent } from './cryptodetail/cryptodetail.component';
import { FilterPipe }from './cryptolist/filter.pipe';
import { OrderByPipe }from './cryptolist/orderby.pipe';
import { WeatherService } from './weather.service';
import {NgxPaginationModule} from 'ngx-pagination';
import { TwitterComponent } from './twitter/twitter.component';
import { TwitterService } from './cryptodetail/twitter.service';
import { NewsapiService } from './cryptodetail/newsapi.service';
import { CoinListService } from './coinlist.service';
import { NewsComponent } from './news/news.component';
import { FooterComponent } from './footer/footer.component';

@NgModule({
  declarations: [
    AppComponent,
    CryptolistComponent,
    CryptodetailComponent,
    FilterPipe,
    OrderByPipe,
    TwitterComponent,
    NewsComponent,
    FooterComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    FormsModule,
    NgxPaginationModule,    
  RouterModule.forRoot([
    {
    path:'coin/:id',
    component :CryptodetailComponent
  },

  {
    path:'',
    component :CryptolistComponent
  },
  {
    path:'news',
    component :NewsComponent
  }

  ])
  ],
  providers: [WeatherService, TwitterService, NewsapiService, CoinListService],
  bootstrap: [AppComponent]
})
export class AppModule { }



