import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import {CoinListService} from '../coinlist.service'

interface ICoin {
  rank : string;
  name: string;
  symbol: number;
  price_usd: string;
  market_cap_usd : string;
  percent_change_24h : string;
  available_supply: string;
  
}
@Component({
  selector: 'app-cryptolist',
  templateUrl: './cryptolist.component.html',
  styleUrls: ['./cryptolist.component.css']
})
export class CryptolistComponent implements OnInit {
  
  crypto = 'List of Crypto currencies';
  selectedRow : Number;
  setClickedRow : Function;
  coins: any[];
  isDesc: boolean = false;
  column: string = 'CategoryName';
  
  constructor(
    private http: HttpClient ,
    private router: Router, 
  private coinListService: CoinListService)  { 

    this.setClickedRow = function(index, coin){
      console.log("clicked");
      this.selectedRow = index;
        }
  }
  ngOnInit() {

    this.coinListService.getCoins().subscribe(
      data => {
          console.log(data);      
          
        this.coins = data;
        console.log(this.coins);      
        console.log(this.coins[0].name);
      }   

); 
    //console.log(this.coins.length + "is the length")

  }
  sort(property){
    this.isDesc = !this.isDesc; //change the direction    
    this.column = property;
    let direction = this.isDesc ? 1 : -1;

    this.coins.sort(function(a, b){
        if(a[property] < b[property]){
            return -1 * direction;
        }
        else if( a[property] > b[property]){
            return 1 * direction;
        }
        else{
            return 0;
        }
    });
};



  
  
 }






