import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import 'rxjs/add/operator/map';


interface INews {
  status: string;
  articles : any [];
}


@Injectable()
export class NewsapiService {

  news: any;
  
  constructor( private http: HttpClient ) { }

  getNews(){
   return  this.http.get<INews>('https://newsapi.org/v2/top-headlines?sources=crypto-coins-news&apiKey=8417ac401f3248cc954739c1d26d7243').map(result => result);
    
  }

}



