import { Component, OnInit } from '@angular/core';
import { WeatherService } from '../weather.service';

import { Chart } from 'chart.js';
import { CoinListService } from '../coinlist.service';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-cryptodetail',
  templateUrl: './cryptodetail.component.html',
  styleUrls: ['./cryptodetail.component.css']
})
export class CryptodetailComponent implements OnInit {
  chart = []; // This will hold our chart info
 
  coin : any;
  coins :any [] ;
  id : any;
  
   constructor(private _weather: WeatherService,
     private _coinlist : CoinListService,
     private route : ActivatedRoute) {}
  
    ngOnInit() {
      this.id  = this.route.snapshot.params['id'];
       this._coinlist.getCoins().subscribe(
        data => {
            console.log(data);      
          this.coins = data;
          console.log(this.coins);      
          console.log(this.coins[0].name);
          var res = this.coins.filter(obj =>{
            return obj.symbol.toLowerCase() ==this.id;
          });
          //this.coin = this.coins[this.id];
          this.coin = res[0];
        }   
  ); 


  

      this._weather.dailyForecast()
        .subscribe(res => {
          let temp_max = res['list'].map(res => res.main.temp_max);
          let temp_min = res['list'].map(res => res.main.temp_min);
          let alldates = res['list'].map(res => res.dt)
          
          let weatherDates = []
          alldates.forEach((res) => {
              let jsdate = new Date(res * 1000)
              weatherDates.push(jsdate.toLocaleTimeString('en', { year: 'numeric', month: 'short', day: 'numeric' }))
          })
          this.chart = new Chart('canvas', {
            type: 'line',
            data: {
              labels: weatherDates,
              datasets: [
                { 
                  data: temp_max,
                  borderColor: "#3cba9f",
                  fill: false
                },
                { 
                  data: temp_min,
                  borderColor: "#ffcc00",
                  fill: false
                },
              ]
            },
            options: {
              legend: {
                display: false
              },
              scales: {
                xAxes: [{
                  display: true
                }],
                yAxes: [{
                  display: true
                }],
              }
            }
          });


        })
    }
 
  
  
}



