import { Component, OnInit } from '@angular/core';
import { TwitterService } from '../cryptodetail/twitter.service';
import { NewsapiService } from '../cryptodetail/newsapi.service';
@Component({
  selector: 'app-news',
  templateUrl: './news.component.html',
  styleUrls: ['./news.component.css']
})
export class NewsComponent implements OnInit {
  tweets: any;
  news  : any [];

  constructor(private _twitter : TwitterService,
    private _news : NewsapiService,) { }

  ngOnInit() {


    this._twitter.getTweets().subscribe(data => {
      console.log(data);      
      console.log(data[0].text);
      this.tweets = data;
      //console.log(data[1].age); 
    });
   this._news.getNews().subscribe(data => {
      console.log(data);      
      console.log(data.articles);
      this.news = data.articles;
      console.log(this.news[0].title); 
    });
  }

}



